// LoadingAnimation.tsx -   
import React from 'react';
import { LoadingUtils } from './LoadingTypes.ts';

interface LoadingAnimationProps {
  progress: number;
}

const LoadingAnimation: React.FC<LoadingAnimationProps> = ({ progress }) => {
  const arrowLength = LoadingUtils.getArrowLength(progress);
  const stage = LoadingUtils.getAnimationStage(progress);
  
  //    
  const getArrowClass = () => {
    if (progress < 25) return 'arrowStage1';
    if (progress < 50) return 'arrowStage2';
    if (progress < 75) return 'arrowStage3';
    return 'arrowStage4';
  };

  return (
    <div className="arrowContainer">
      <div 
        className={`arrow ${getArrowClass()}`}
        style={{
          width: `${arrowLength * 2.4}px`, //  240px
        }}
      >
        <div className="arrowHead" />
      </div>
    </div>
  );
};

export default LoadingAnimation;