// LoadingScreen.tsx -    
import React, { useState, useEffect, useCallback } from 'react';
import { 
  LoadingState, 
  LoadingAPI, 
  LoadingUtils,
  LoadingResponse,
  LoadingError
} from './LoadingTypes.ts';
import LoadingAnimation from './LoadingAnimation.tsx';
import GeometricBackground from './GeometricBackground.tsx';
import './Loading.css';

interface LoadingScreenProps {
  onLoadingComplete: (redirectPath: string) => void;
}

const LoadingScreen: React.FC<LoadingScreenProps> = ({ onLoadingComplete }) => {
  const [loadingState, setLoadingState] = useState<LoadingState>({
  isLoading: true,
  isReady: false,
  progress: 0,
  error: null,
  showRetryButton: false,
  message: '',          
});


  //   
  const startProgressAnimation = useCallback(() => {
    let currentProgress = 0;
    const progressInterval = setInterval(() => {
      currentProgress += Math.random() * 8 + 2; // 2-10 
      
      if (currentProgress >= 100) {
        currentProgress = 100;
        clearInterval(progressInterval);
      }
      
      setLoadingState(prev => ({
        ...prev,
        progress: currentProgress
      }));
    }, 150); // 150ms 

    return progressInterval;
  }, []);

  //  
  const initializeApp = useCallback(async () => {
    try {
      setLoadingState(prev => ({
        ...prev,
        isLoading: true,
        error: null,
        showRetryButton: false
      }));

      //   
      const progressInterval = startProgressAnimation();

      // API  (    )
      const response = await LoadingAPI.generateMockResponse(); //  LoadingAPI.initializeApp() 
      
      //    
      if ('isReady' in response) {
        const loadingResponse = response as LoadingResponse;
        
        if (loadingResponse.isReady) {
          //  100%  
          const waitForProgress = () => {
            if (loadingState.progress >= 100) {
              setTimeout(() => {
                onLoadingComplete(loadingResponse.redirect);
              }, 500); //   0.5 
            } else {
              setTimeout(waitForProgress, 100);
            }
          };
          waitForProgress();
        } else {
          // 3   
          setTimeout(() => {
            onLoadingComplete(loadingResponse.redirect);
          }, loadingResponse.delay);
        }
        
        setLoadingState(prev => ({
          ...prev,
          isReady: loadingResponse.isReady
        }));
      } else {
        //   
        const errorResponse = response as LoadingError;
        throw new Error(errorResponse.message);
      }

      //   
      clearInterval(progressInterval);
      
    } catch (error) {
      console.error('  :', error);
      
      setLoadingState(prev => ({
        ...prev,
        isLoading: false,
        error: error instanceof Error ? error.message : '    .',
        showRetryButton: true
      }));
    }
  }, [onLoadingComplete, startProgressAnimation, loadingState.progress]);

  //  
  const handleRetry = useCallback(() => {
    setLoadingState({
      isLoading: true,
      isReady: false,
      progress: 0,
      error: null,
      showRetryButton: false,
      message: '',
    });
    initializeApp();
  }, [initializeApp]);

  // 5  
  useEffect(() => {
    const timeoutId = setTimeout(() => {
      if (loadingState.isLoading && !loadingState.isReady) {
        setLoadingState(prev => ({
          ...prev,
          isLoading: false,
          error: '  .   .',
          showRetryButton: true
        }));
      }
    }, 5000);

    return () => clearTimeout(timeoutId);
  }, [loadingState.isLoading, loadingState.isReady]);

  //     
  useEffect(() => {
    initializeApp();
  }, [initializeApp]);

  // 3   
  useEffect(() => {
    const fallbackTimeout = setTimeout(() => {
      if (loadingState.isLoading) {
        onLoadingComplete('/main');
      }
    }, 3000);

    return () => clearTimeout(fallbackTimeout);
  }, [onLoadingComplete, loadingState.isLoading]);

  return (
    <div className="loadingContainer">
      {/*    */}
      <GeometricBackground />
      
      {/*    */}
      {loadingState.error ? (
        //  
        <div className="errorContainer">
          <div className="errorTitle"> </div>
          <div className="errorMessage">{loadingState.error}</div>
          {loadingState.showRetryButton && (
            <button 
              className="retryButton"
              onClick={handleRetry}
            >
               
            </button>
          )}
        </div>
      ) : (
        //   
        <>
          <div className="logoContainer">
            <div className="logoText">
              <div className="logoMain">AWS</div>
              <div className="logoAccent">GIoT</div>
            </div>
            <div className="logoSubtext">Air Watch System</div>
          </div>
          
          <LoadingAnimation progress={loadingState.progress} />
          
          <div className="loadingText">
              ...
          </div>
        </>
      )}
      
      {/*   */}
      <div className="copyright">
        2025 GBSA AWS
      </div>
    </div>
  );
};

export default LoadingScreen;