// Dashboard.tsx -   
import React, { useState, useEffect } from 'react';
import { LineChart, Line, AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import { Bell, User, ChevronDown, Info } from 'lucide-react';
import { 
  NotificationData, 
  SensorData, 
  SensorType, 
  SidebarItemProps,
  DashboardAPI,
  DashboardUtils,
  SENSOR_OPTIONS,
  MENU_ITEMS
} from '../../services/Dashboard.ts';
import './Dashboard.css';

//   
const SidebarItem: React.FC<SidebarItemProps> = ({ icon, label, isActive, onClick }) => (
  <button
    onClick={onClick}
    className={`sidebarItem ${isActive ? 'sidebarItemActive' : ''}`}
  >
    <span>{icon}</span>
    <span>{label}</span>
  </button>
);

//   
const NotificationDropdown: React.FC<{ 
  isOpen: boolean; 
  onClose: () => void; 
  notifications: NotificationData['notifications'];
}> = ({ isOpen, onClose, notifications }) => {
  if (!isOpen) return null;

  return (
    <>
      <div className="dropdown">
        <div className="dropdownHeader">
          <h3 className="dropdownTitle"></h3>
        </div>
        <div className="notificationList">
          {notifications.length === 0 ? (
            <div className="emptyNotification">
                
            </div>
          ) : (
            notifications.map((notification) => (
              <div 
                key={notification.id} 
                className={`notificationItem ${!notification.read ? 'notificationItemUnread' : ''}`}
              >
                <p className="notificationMessage">{notification.message}</p>
                <p className="notificationTimestamp">{notification.timestamp}</p>
              </div>
            ))
          )}
        </div>
      </div>
      <button 
        onClick={onClose}
        className="dropdownOverlay"
        aria-label=" "
      />
    </>
  );
};

//   
const AdminDropdown: React.FC<{ isOpen: boolean; onClose: () => void }> = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <>
      <div className="adminDropdown">
        <div className="adminDropdownContent">
          <button className="adminDropdownItem"> </button>
          <button className="adminDropdownItem"> </button>
          <div className="adminDropdownDivider" />
          <button className="adminDropdownItem adminDropdownLogout"></button>
        </div>
      </div>
      <button 
        onClick={onClose}
        className="dropdownOverlay"
        aria-label="  "
      />
    </>
  );
};

//   
const SensorChart: React.FC<{ 
  sensorData: SensorData | null;
  isLoading: boolean;
  error: string | null;
}> = ({ sensorData, isLoading, error }) => {
  if (isLoading) {
    return (
      <div className="loadingContainer">
        <div>  ...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="errorContainer">
        <div className="errorTitle">  </div>
        <div className="errorMessage">{error}</div>
      </div>
    );
  }

  if (!sensorData || !sensorData.success) {
    return (
      <div className="errorContainer">
        <div className="errorTitle">   </div>
      </div>
    );
  }

  const chartData = sensorData.labels.map((label, index) => ({
    time: label,
    value: sensorData.values[index]
  }));

  const color = DashboardUtils.getChartColor(sensorData.sensorType);

  return (
    <div className="chartContainer">
      <ResponsiveContainer width="100%" height="100%">
        {sensorData.sensorType === 'gas' ? (
          <AreaChart data={chartData}>
            <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
            <XAxis 
              dataKey="time" 
              stroke="#666"
              fontSize={12}
            />
            <YAxis 
              stroke="#666"
              fontSize={12}
            />
            <Tooltip 
              contentStyle={{
                backgroundColor: 'white',
                border: '1px solid #e5e7eb',
                borderRadius: '6px'
              }}
            />
            <Area 
              type="monotone" 
              dataKey="value" 
              stroke={color}
              fill={color}
              fillOpacity={0.3}
              strokeWidth={2}
            />
          </AreaChart>
        ) : (
          <LineChart data={chartData}>
            <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
            <XAxis 
              dataKey="time" 
              stroke="#666"
              fontSize={12}
            />
            <YAxis 
              stroke="#666"
              fontSize={12}
            />
            <Tooltip 
              contentStyle={{
                backgroundColor: 'white',
                border: '1px solid #e5e7eb',
                borderRadius: '6px'
              }}
            />
            <Line 
              type="monotone" 
              dataKey="value" 
              stroke={color}
              strokeWidth={2}
              dot={{ fill: color, strokeWidth: 2, r: 4 }}
              activeDot={{ r: 6, stroke: color, strokeWidth: 2 }}
            />
          </LineChart>
        )}
      </ResponsiveContainer>
    </div>
  );
};

//   
const Dashboard: React.FC = () => {
  const [activeMenu, setActiveMenu] = useState('Dashboard');
  const [notificationData, setNotificationData] = useState<NotificationData>({
    count: 0,
    notifications: []
  });
  const [isNotificationOpen, setIsNotificationOpen] = useState(false);
  const [isAdminMenuOpen, setIsAdminMenuOpen] = useState(false);
  const [selectedSensor, setSelectedSensor] = useState<SensorType>('temperature');
  const [sensorData, setSensorData] = useState<SensorData | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [allSensorData, setAllSensorData] = useState<Record<SensorType, SensorData | null>>({
  temperature: null,
  humidity: null,
  gas: null,
});

  //   
  const fetchSensorData = async (sensorType: SensorType) => {
    setIsLoading(true);
    setError(null);
    
    try {
      const data = await DashboardAPI.getSensorData(sensorType);
      
      if (data.success) {
        setSensorData(data as SensorData);
        setAllSensorData(prev => ({
          ...prev,
          [sensorType]: data as SensorData
        }));
      } else {
        setError('   .');
      }
    } catch (err) {
      setError('    .');
      console.error('   :', err);
    } finally {
      setIsLoading(false);
    }
  };

  //  REPLACE  
const fetchAllSensorData = async () => {
  try {
    const results = await Promise.all(
      SENSOR_OPTIONS.map(opt => DashboardAPI.getSensorData(opt.value as SensorType))
    );

    const newAllSensorData: Record<SensorType, SensorData | null> = {
      temperature: null,
      humidity: null,
      gas: null,
    };

    results.forEach((result, index) => {
      if (result.success) {
        const sensorType = SENSOR_OPTIONS[index].value as SensorType;
        newAllSensorData[sensorType] = result as SensorData;
      }
    });

    setAllSensorData(newAllSensorData);
  } catch (err) {
    console.error('    :', err);
  }
};

  //   
  const fetchNotifications = async () => {
    try {
      const data = await DashboardAPI.getNotifications();
      setNotificationData(data);
    } catch (error) {
      console.error('   :', error);
    }
  };

  //   
  const handleMenuClick = (label: string, path: string) => {
    setActiveMenu(label);
    console.log(`Navigating to: ${path}`);
  };

  //   
  const handleSensorSelect = (sensorType: SensorType) => {
    setSelectedSensor(sensorType);
    fetchSensorData(sensorType);
  };

  //      
  useEffect(() => {
    fetchNotifications();
    fetchSensorData('temperature'); // 
    fetchAllSensorData(); //   
    
    //    (30)
    const interval = setInterval(() => {
      fetchNotifications();
      fetchSensorData(selectedSensor);
      fetchAllSensorData();
    }, 30000);
    
    return () => clearInterval(interval);
  }, [selectedSensor]);

  //       
  useEffect(() => {
    if (allSensorData[selectedSensor]) {
      setSensorData(allSensorData[selectedSensor]);
    }
  }, [selectedSensor, allSensorData]);

  //     useEffect 
  const [currentTime, setCurrentTime] = useState(DashboardUtils.getCurrentDateTime());
  
  useEffect(() => {
    const timeInterval = setInterval(() => {
      setCurrentTime(DashboardUtils.getCurrentDateTime());
    }, 60000); // 1 
    
    return () => clearInterval(timeInterval);
  }, []);

  return (
    <div className="dashboardContainer">
      {/*  */}
      <nav className="sidebar">
        <div className="sidebarHeader">
          <h2 className="sidebarTitle">AWS IOT</h2>
        </div>

        <div className="sidebarMenu">
          {MENU_ITEMS.map((item) => (
            <SidebarItem
              key={item.label}
              icon={item.icon}
              label={item.label}
              isActive={activeMenu === item.label}
              onClick={() => handleMenuClick(item.label, item.path)}
            />
          ))}
        </div>
      </nav>

      {/*    */}
      <main className="mainContent">
        {/*   */}
        <header className="header">
          <div className="headerLeft">
            <h1 className="pageTitle">{activeMenu}</h1>
            <p className="pageSubtitle">{currentTime}</p>
          </div>
          
          <div className="headerRight">
            {/*   */}
            <div className="headerItem">
              <button
                onClick={() => {
                  setIsNotificationOpen(!isNotificationOpen);
                  setIsAdminMenuOpen(false);
                }}
                className="headerButton"
                aria-label=""
              >
                <Bell size={20} />
                {notificationData.count > 0 && (
                  <span className="notificationBadge">
                    {notificationData.count > 99 ? '99+' : notificationData.count}
                  </span>
                )}
              </button>
              
              <NotificationDropdown
                isOpen={isNotificationOpen}
                onClose={() => setIsNotificationOpen(false)}
                notifications={notificationData.notifications}
              />
            </div>

            {/*   */}
            <div className="headerItem">
              <button
                onClick={() => {
                  setIsAdminMenuOpen(!isAdminMenuOpen);
                  setIsNotificationOpen(false);
                }}
                className="adminButton"
                aria-label=" "
              >
                <User size={20} />
                <span></span>
                <ChevronDown size={16} />
              </button>
              
              <AdminDropdown
                isOpen={isAdminMenuOpen}
                onClose={() => setIsAdminMenuOpen(false)}
              />
            </div>
          </div>
        </header>

        {/*    */}
        <div className="dashboardContent">
          {activeMenu === 'Dashboard' ? (
            <>
              {/*     */}
              <section className="chartSection">
                <div className="sectionHeader">
                  <h2 className="sectionTitle">TIME-AVERAGED DATA</h2>
                  
                  {/*    */}
                  <div className="sensorSelector">
                    <select
                      value={selectedSensor}
                      onChange={(e) => handleSensorSelect(e.target.value as SensorType)}
                      className="sensorSelect"
                    >
                      {SENSOR_OPTIONS.map((option) => (
                        <option key={option.value} value={option.value}>
                          {option.label}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>

                <div className="chartCard">
                  <SensorChart 
                    sensorData={sensorData}
                    isLoading={isLoading}
                    error={error}
                  />
                </div>
              </section>

              {/*  &     */}
              <section className="summarySection">
                <div className="sectionHeader">
                  <h2 className="sectionTitle">CURRENT &amp; PREDICTION DATA</h2>
                  <div className="infoIcon">
                    <Info size={16} />
                  </div>
                </div>
                
                <div className="summaryCard">
                  <table className="summaryTable">
                    <thead>
                      <tr>
                        <th>TIME</th>
                        <th>TEMPERATURE</th>
                        <th>HUMIDITY</th>
                        <th>GAS CONCENTRATION</th>
                      </tr>
                    </thead>
                    <tbody>
                      {/*    */}
                      <tr>
                        <td>CURRENT DATA</td>
                        <td>
                          {allSensorData.temperature ? (
                            <span className={DashboardUtils.getStatusClass(allSensorData.temperature.current.status)}>
                              {allSensorData.temperature.current.value.toFixed(1)}{allSensorData.temperature.unit}
                            </span>
                          ) : (
                            <span> ...</span>
                          )}
                        </td>
                        <td>
                          {allSensorData.humidity ? (
                            <span className={DashboardUtils.getStatusClass(allSensorData.humidity.current.status)}>
                              {allSensorData.humidity.current.value.toFixed(1)}{allSensorData.humidity.unit}
                            </span>
                          ) : (
                            <span> ...</span>
                          )}
                        </td>
                        <td>
                          {allSensorData.gas ? (
                            <span className={DashboardUtils.getStatusClass(allSensorData.gas.current.status)}>
                              {allSensorData.gas.current.value.toFixed(0)}{allSensorData.gas.unit}
                            </span>
                          ) : (
                            <span> ...</span>
                          )}
                        </td>
                      </tr>
                      
                      {/*    */}
                      <tr>
                        <td>PREDICTION DATA</td>
                        <td>
                          {allSensorData.temperature ? (
                            <span>{allSensorData.temperature.prediction.value.toFixed(1)}{allSensorData.temperature.unit}</span>
                          ) : (
                            <span> ...</span>
                          )}
                        </td>
                        <td>
                          {allSensorData.humidity ? (
                            <span>{allSensorData.humidity.prediction.value.toFixed(1)}{allSensorData.humidity.unit}</span>
                          ) : (
                            <span> ...</span>
                          )}
                        </td>
                        <td>
                          {allSensorData.gas ? (
                            <span>{allSensorData.gas.prediction.value.toFixed(0)}{allSensorData.gas.unit}</span>
                          ) : (
                            <span> ...</span>
                          )}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </section>
            </>
          ) : (
            //     
            <div style={{ textAlign: 'center', padding: '80px 20px' }}>
              <h2 style={{ fontSize: '24px', fontWeight: 'bold', color: '#374151', marginBottom: '16px' }}>
                {activeMenu} 
              </h2>
              <p style={{ color: '#6b7280', marginBottom: '8px' }}>
                  : {activeMenu}
              </p>
              <p style={{ fontSize: '14px', color: '#9ca3af' }}>
                    .
              </p>
            </div>
          )}
        </div>
      </main>
    </div>
  );
};

export default Dashboard;